import React from "react";
import { Button, Icon } from "rsuite";
import withStyles from "react-jss";

const styles = {
  toggleButton: {
    marginLeft: 10,
    marginRight: 23,
  },
};

const RV_SidebarToggleButton = ({ classes }) => (
  <Button className={classes.toggleButton}>
    <Icon icon="bars" />
  </Button>
);

export default withStyles(styles)(RV_SidebarToggleButton);
